import Mock from 'mockjs'
let Random = Mock.Random

let plan_name = ['','','','',
    '','']
let plan_list = [

    {
        id :Random.increment(),
        name: '',
        region: Random.county(true),
        person:Random.cname(),
        thing:'',
        except:'',
    },
    {
        id :Random.increment(),
        name: Random.pick(plan_name),
        region: Random.county(true),
        person:Random.cname(),
        thing:'',
        except:'',
    },
    {
        id :Random.increment(),
        name: Random.pick(plan_name),
        region: Random.county(true),
        person:Random.cname(),
        thing:'',
        except:'',
    },
    {
        id :Random.increment(),
        name: Random.pick(plan_name),
        region: Random.county(true),
        person:Random.cname(),
        thing:'',
        except:'',
    },
]

Mock.mock('/api/plan/plan_list', 'get', () => {
    return {
        code: 0,
        plan: plan_list
    }
})

Mock.mock('/api/plan/plan_add', 'post', (options) => {
    console.log('options %o', options)

    let newplan = JSON.parse(options.body)
    newplan['id'] = Random.increment()

    plan_list.push(newplan)

    return {
        code: 0,
        plan: plan_list
    }
})


